import React from "react";
import withSection from "./withSection";
import FormInputRow from "../../form-inputs/FormInputRow";
import { Consumer } from "../roi.context";

class SectionTeam extends React.PureComponent {
  render() {
    return (
      <Consumer>
        {({
          numberOfUsers,
          averageAnnualSalary,
          fullyLoadedCostMultiplier,
          averageUserFullyLoadedCost,
          averageUserFullyLoadedCostHourly
        }) => (
          <React.Fragment>
            <FormInputRow
              label="Number of users"
              defaultValue={numberOfUsers}
              onBlur={this.props.onUpdate}
              name="numberOfUsers"
            />
            <FormInputRow
              label="Average user annual salary"
              defaultValue={averageAnnualSalary}
              format="currency"
              onBlur={this.props.onUpdate}
              name="averageAnnualSalary"
            />
            <FormInputRow
              label="Fully-loaded cost (multiplier)"
              defaultValue={fullyLoadedCostMultiplier}
              format="float"
              onBlur={this.props.onUpdate}
              name="fullyLoadedCostMultiplier"
            />
            <FormInputRow
              label="Average user fully-loaded (annual)"
              defaultValue={averageUserFullyLoadedCost}
              format="currency"
              readOnly={true}
            />
            <FormInputRow
              label="Average user fully-loaded (hourly)"
              defaultValue={averageUserFullyLoadedCostHourly}
              format="currency"
              readOnly={true}
            />
          </React.Fragment>
        )}
      </Consumer>
    );
  }
}

export default withSection(SectionTeam, "Team");
