import React from "react";
import withSection from "./withSection";
import FormInputRow from "../../form-inputs/FormInputRow";
import { Consumer } from "../roi.context";

class SectionCost extends React.Component {
  render() {
    return (
      <Consumer>
        {({ contractCost, perUserCost }) => (
          <React.Fragment>
            <FormInputRow
              label="Per user cost (monthly)"
              defaultValue={perUserCost}
              format="currency"
              onBlur={this.props.onUpdate}
              name="perUserCost"
            />
            <FormInputRow
              label="Contract cost"
              defaultValue={contractCost}
              format="currency"
              readOnly={true}
            />
          </React.Fragment>
        )}
      </Consumer>
    );
  }
}

export default withSection(SectionCost, "Cost");
