import React from "react";
import {
  SectionCost,
  SectionTeam,
  SectionProductivity,
  SectionOptimization,
  SectionCalculator
} from "./sections";
import * as Lib from "./roi-calculator.lib";
import { Provider } from "./roi.context";

export default class RoiCalculator extends React.PureComponent {
  timer = null;
  previousKey = null;
  previousValue = null;
  state = {
    hasInitialized: false
  };
  componentDidMount() {
    Lib.initDefaultValues.call(this);
  }

  onUpdate = async (key, value, formattedValue) => {
    if (key === this.previousKey && value === this.previousValue) {
      return;
    }
    this.previousKey = key;
    this.previousValue = value;
    await Lib.updateStateValues.call(this, key, value, formattedValue);
    Lib.updateCalculations.call(this, key, value, formattedValue);
  };
  render() {
    const contextValue = this.state;
    return (
      <React.Fragment>
        {this.state.hasInitialized && (
          <Provider value={contextValue}>
            <div className="inputs-container">
              <div className="calculator-container-top">
                Roi Calculator Container area
              </div>
              <div className="flex-row">
                <div className="inputs-body flex-col-60">
                  <SectionTeam onUpdate={this.onUpdate} />
                  <SectionOptimization onUpdate={this.onUpdate} />
                  <SectionProductivity onUpdate={this.onUpdate} />
                  <SectionCost onUpdate={this.onUpdate} />
                </div>
                <div className="calculator-body flex-col-40">
                  <SectionCalculator />
                </div>
              </div>
            </div>
          </Provider>
        )}
      </React.Fragment>
    );
  }
}
