export const currency = value => {
  value = value + "";
  const reg = new RegExp("[^0-9|.]", "gi");
  let number = value.replace(reg, "");
  const returnValue = Number(number).toLocaleString("en-US", {
    style: "currency",
    currency: "USD"
  });
  return returnValue;
};

export const float = value => {
  if (!value) {
    return 0;
  }
  const reg = new RegExp("[^0-9|.]", "gi");
  value += "";
  let formattedValue = value.replace(reg, "");
  formattedValue = parseFloat(formattedValue).toFixed(2);
  if (isNaN(formattedValue)) {
    return 0;
  }
  return formattedValue;
};

export const number = value => {
  if (!value) {
    return 0;
  }
  const reg = new RegExp("[^0-9|.]", "gi");
  value += "";
  let formattedValue = value.replace(reg, "").toLocaleString("en-US");
  if (isNaN(formattedValue)) {
    return 0;
  }
  return formattedValue;
};
