import React from "react";
import * as Utils from "../../lib/utils";

export default class FormInputRow extends React.PureComponent {
  state = {
    value: ""
  };

  componentDidMount() {
    this.setState({
      value: this.setValue(this.props.defaultValue)
    });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.defaultValue !== this.props.defaultValue) {
      this.setState({
        value: this.setValue(this.props.defaultValue)
      });
    }
  }

  setValue = value => {
    let formatted;
    switch (this.props.format) {
      case "number":
        formatted = Utils.number(value);
        break;

      case "currency":
        formatted = Utils.currency(value);
        break;

      case "float":
        formatted = Utils.float(value);
        break;

      default:
        formatted = value;
        break;
    }
    return formatted;
  };

  updateOnBlur = () => {
    this.setState(
      prevState => ({
        value: this.setValue(prevState.value)
      }),
      () => {
        if (this.props.onBlur) {
          console.log(this.state.value);
          this.props.onBlur(this.props.name, this.state.value);
        }
      }
    );
  };

  render() {
    return (
      <div className="flex-row">
        <label>{this.props.label}</label>
        <input
          type="text"
          className="text-input"
          name={this.props.name}
          value={this.state.value}
          onChange={e => this.setState({ value: e.target.value })}
          onBlur={this.updateOnBlur}
          readOnly={this.props.readOnly}
        />
      </div>
    );
  }
}
