import React from "react";
import withSection from "./withSection";
import FormInputRow from "../../form-inputs/FormInputRow";
import { Consumer } from "../roi.context";

class SectionProductivity extends React.Component {
  render() {
    return (
      <Consumer>
        {({ hoursSaved, daysSaved, annualSavings }) => (
          <React.Fragment>
            <FormInputRow
              label="Hours saved per month, per team member"
              defaultValue={hoursSaved}
              readOnly={true}
              name="hoursSaved"
            />
            <FormInputRow
              label="Days saved per month, per team member"
              defaultValue={daysSaved}
              readOnly={true}
              name="daysSaved"
            />
            <FormInputRow
              label="Annual team productivity savings"
              defaultValue={annualSavings}
              format="currency"
              readOnly={true}
              name="annualSavings"
            />
          </React.Fragment>
        )}
      </Consumer>
    );
  }
}

export default withSection(SectionProductivity, "Productivity");
